@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

html,body{
	height:100%;

	/* .@{container-class}{
		background: #fff;
	
		padding-bottom:0;
	} */
}
.fn-@{nmq-ns}-myinfo{
	@pd:3.2%;
	.info-item{
		padding-left: @pd;
		background: #fff;

		.list{
			padding: 8px @pd 8px 0;
			border-bottom: 1px solid @border-color-base;
			overflow: hidden;

			.item-img{
				display:block;
				//width:20%;

				img{
					border-radius:100%;
					.absolute(0,_,_,0);
					width: 100%;

				}
			}

			.text{
				padding:2% 0 0 2%;
				//width: 80%;
				position:relative;

				.tit{
					color: #333;
					font-weight: normal;
					font-size: @font-size-lg;
				}

				.detail{
					font-size: @font-size-base;
					color: #666;
					.ellipsis();

					em{
						color: @fu-color;
					}
				}

				.date{
					color: #999;
					padding: 2% 0 0;
					display: flex;
					align-items: center;

		    		&:before{
						.nmq-icon();
						content:"\e604";
						font-size: @font-size-lg;
						padding-right:3px;
					}

					span{
						font-size: @font-size-base;
						display: inline-block;
					}
				}
			}
		}
	}
}
